import React, { useEffect, useState } from "react";
import { Card, CardBody, CardHeader, Col, Row, Progress } from "reactstrap";
import { toast, ToastContainer } from "react-toastify";
import axios from "axios";
import Spinner from "reactstrap/lib/Spinner";
import Classes from "./Comments.module.css";

const CommentsDetails = (props) => {
  const { id } = props.match.params;
  const [commentInfo, setCommentInfo] = useState([]);
  if (!id) {
    props.history.push("/dashboard");
  }

  useEffect(() => {
    axios({
      url: "/",
      method: "post",
      data: {
        query: `
        query getAllComment($page:Int,$limit:Int,$productId:ID,$commentId:ID){
            getAllComment(page:$page,limit:$limit,productId:$productId,commentId:$commentId){
              user{
                _id,
                fname,
                phone
              },
            product{
                fname
              },
              title,
              description,
              negative,
              positive,
              vsurvey{
                survey{
                  name
                },
                value
              },
              status
            }
          }
        `,
        variables: {
          page: 1,
          limit: 30,
          productId: null,
          commentId: id,
        },
      },
    })
      .then((res) => {
        if (res.data.errors) {
          toast.error("خطا در دریافت اطلاعات");
        } else {
          setCommentInfo(res.data.data.getAllComment);
        }
      })
      .catch((err) => console.log(err));
  }, [id]);

  console.log(commentInfo);

  return (
    <div className="animated fadeIn">
      <ToastContainer />
      <Row>
        <Col lg={12}>
          <Card>
            <CardHeader>
              <strong>
                <i className="icon-info pr-1"></i>عنوان نظر: {commentInfo.title}
              </strong>
            </CardHeader>
            <CardBody>
              <Row>
                {commentInfo.length > 0 ? (
                  <>
                    <Col>
                      نام کاربر :{commentInfo[0].user.fname}
                      شماره همراته : {commentInfo[0].user.phone}
                    </Col>
                    <Col>
                      <div className={Classes.positive}>
                        نقاط قوت{" "}
                        {commentInfo[0].positive.map((item) => (
                          <p>{item}</p>
                        ))}
                      </div>
                      <div className={Classes.negative}>
                        نقاط ضعف{" "}
                        {commentInfo[0].negative.map((item) => (
                          <p>{item}</p>
                        ))}
                      </div>
                      <div className="my-5">
                        {commentInfo[0].vsurvey.map((item, index) => (
                          <>
                            <p className={Classes.txtP}>{item.survey.name}</p>
                            {/* <span className={Classes.txtSpan}>{item.value} از 5 نمره</span> */}
                            <div className="text-center">{item.value} از 5 واحد</div>
                            <Progress value={item.value * 10} />
                          </>
                        ))}
                      </div>
                    </Col>
                  </>
                ) : (
                  <center>
                    {" "}
                    <Spinner size="sm" />
                  </center>
                )}
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    </div>
  );
};

export default CommentsDetails;
